                        video:[""],
                        link:[''],
                    src:["https://www.inarts.edu.tw//uploads/plan/cover/167/thumb_1111.jpg","https://www.inarts.edu.tw//uploads/plan/cover/72/thumb_23.jpg","https://www.inarts.edu.tw//uploads/plan/cover/138/thumb_12.jpg",],text: ['  　','跨領域美感計畫應教育部邀請至基隆市舉辦107年度文化教育科長研習，由澎南國中碩果有成的影片開場，協同主持人 陳韻文副教授帶領大家了解「什麼是跨領域美感計畫」，並邀請在地達人藝術家 王傑，與最佳拍檔建築師 詹益忠，藉長榮桂冠19樓的風景一覽基隆風貌。','藝術家 王傑用蕨類美學比喻今日基隆，用清朝街長(相當於現代的市長) -許梓桑寫的基隆八景詩詞為題，引領大家透過畫作的視野，對照窗景認識基隆，在最後提筆現場揮毫水彩即興創作示範，讓大家一飽眼福！也透過大師觀摩，嘗試下筆創作。','每位參加的科員與科長皆拿到不同的八景詩詞明信片，用筆畫下自己屬意的窗景，耳聽詹益忠老師精闢歷練的在地歷史脈絡的解說，將視覺經驗轉化為個人化的創作，寄出貼好郵票的明信片，讓日後還能再度回味在基隆創造的美感經驗。','美感教育不同於藝術教育，重要的是召喚人的感受力和想像力，不用被既定的品評標的給限制住，另一方面則是美感教育需要時間醞釀醱酵，急不得也。','最後，計畫以哲人說 “ 如果我們都能珍惜腳下的風土，就沒有文化貧瘠、心靈荒蕪之處。”作為工作坊結尾，每人用心體會，美感經驗何嘗不是唾手可得呢?',' ',] }
                },{
                        src:"https://www.inarts.edu.tw//uploads/plan/cover/72/thumb_23.jpg",
                        type: '會議記錄',
                        
                    title: '1070916九月跨域美感走讀《屏東場-滿載溫柔陽光-林仔邊》', 
