                    content: {
                        video:[""],
                        link:[''],
                    src:["https://www.inarts.edu.tw//uploads/plan/cover/72/thumb_23.jpg","https://www.inarts.edu.tw//uploads/plan/cover/213/thumb_CIMG7649.JPG","https://www.inarts.edu.tw//uploads/plan/cover/249/thumb_37074002_2201611900067811_6166635480055021568_n.jpg",],text: [' ','叮叮叮~屏東走讀開始囉~雖然風雨不小，但是陽光☀後來漸漸露臉，一同參與走讀的行列令人開心❤','富有歷史文化及鄉鎮特色的林邊鄉，由熱情的計畫師培教授-鄭方靖教授，用令人懷念的民謠帶領大家唱跳開場去霉味，歡樂出發！','一個城鎮的形成及改變，需要許許多多的累積，六星級走讀人-陳錦超醫生，逆風帶領著腳踏車隊，在毛雨中，告訴大家地名歷史的由來，還有運用社區營造，改變在地...等，許許多多小鄉鎮裡藏著的大秘密。','歡樂的午餐時間，運用在地有機土地種植出來的食材們，自由拼貼創作自己的午餐窯烤披薩，大家吃的開心，學習也是豐碩滿滿✧','透過美感跨域的視野，我們都需要重新注視自己的城鎮，因為那是不只是屬於我們的回憶，也是我們的開始。','104/4/17 國立屏東高級中學 實驗課程循環II 第一次工作坊','107/04/12宜蘭縣羅東鎮成功國民小學張連強助理教授訪視會談','107/07/14全台《美感跨域·在地走讀工作坊》新北猴硐場次-蜂蠟布自造班',] }
