                    content: {
                        video:[""],
                        link:[''],
                    src:["https://www.inarts.edu.tw//uploads/plan/cover/8/thumb_IMG_4598.JPG","https://www.inarts.edu.tw//uploads/plan/cover/249/thumb_37074002_2201611900067811_6166635480055021568_n.jpg","https://www.inarts.edu.tw//uploads/plan/cover/80/thumb_31.JPG",],text: ['全台《美感跨域·在地走讀工作坊》臺南場次-無米樂社區 奔跑吧！農夫開跑囉！','與芒果遊戲團隊共同策劃，在菁寮無米樂社區帶領參與民眾進行街區文化走讀後，參加者化身兼具勇氣與智慧的大小偵探，靠著線索指示破解重重難關，親身體驗實境遊戲，遊戲融入歷史、菁寮無米樂社區的百年文化、建築美感等領域，帶領參加者從遊戲中經驗與學習。芒果遊戲團隊更熱情的與民眾分享以歷史文化設計的遊戲故事、洋蔥染DIY，共同度過週末的美好午後時光。','感謝協同主持人李其昌副教授、合作學校臺南大學音樂學系鄭方靖教授蒞臨參與，支持並活潑地分享計畫想法，帶領我們從在地文化經驗，真正落實生活中的美感教育。','105/5/11 南區高中職組招募說明會','107/07/14全台《美感跨域·在地走讀工作坊》新北猴硐場次-蜂蠟布自造班','103/9/25 國立臺東高級中學訪視會議暨掛牌儀式',] }
